.hamburger__container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 30px;
  height: 25px;
  height: 100%;
  width: 100%; }
  .hamburger__container:focus {
    outline: none; }

.hamburger__bars {
  position: absolute;
  width: 30px;
  height: 2px;
  background: #ffffff;
  transition: transform 220ms 120ms ease-in-out; }
  .hamburger__bars::before, .hamburger__bars::after {
    display: block;
    position: absolute;
    width: 30px;
    height: 2px;
    background: #ffffff;
    content: '';
    top: 0; }
  .hamburger__bars::before {
    top: 10px;
    transition: top 100ms 250ms ease-in,  transform 220ms ease-in-out; }
  .hamburger__bars::after {
    top: -10px;
    transition: bottom 100ms 250ms ease-in,  transform 220ms ease-in-out; }
  .hamburger__bars:focus {
    outline: none; }

.hamburger--active {
  z-index: 101;
  background-color: transparent; }
  .hamburger--active .hamburger__bars {
    transform: rotate(225deg); }
    .hamburger--active .hamburger__bars:before {
      top: 0;
      transform: rotate(-90deg); }
    .hamburger--active .hamburger__bars:after {
      top: 0;
      transform: rotate(90deg); }

[class="navigation__list--active"] ul {
  display: flex; }

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(-100px); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

@keyframes slideOut {
  0% {
    opacity: 1;
    transform: translateX(0); }
  100% {
    opacity: 0;
    transform: translateY(-100px); } }

